<template>
    <section class="m-sear-bar">
        <div class="search-nav">
            <div class="search-option brand" @click="getBrand">品牌<i class="icon-arrow-1"></i></div>
            <div class="search-option firstpay" @click="optionClick($event,'firstpay')" data-index = 0>首付<i :class="activeIndex == 0 ?'icon-arrow-2' :'icon-arrow-1'"></i></div>
            <div class="search-option monthpay" @click="optionClick($event,'monthpay')" data-index = 1>月供<i :class="activeIndex == 1 ?'icon-arrow-2' :'icon-arrow-1'"></i></div>
            <div class="search-option carprice" @click="optionClick($event,'carprice')" data-index = 2>车价<i :class="activeIndex == 2 ?'icon-arrow-2' :'icon-arrow-1'"></i></div>
            <div class="search-option carstyle" @click="optionClick($event,'carstyle')" data-index = 3>车型<i :class="activeIndex == 3 ?'icon-arrow-2' :'icon-arrow-1'"></i></div>
            <div class="search-option option-reset" @click="resetOption()"><i class="icon-reset">重置</i></div>
            <div class="m-drop-menu" v-show="isShow">
                <ul class="option-list clearfix" v-if='showArr.length'>
                    <li v-for='(item,index) in showArr' :key="index" :class="index == itemIndex ? className+' active': className" @click="setOption(item.value,index)">
                        <a>{{item.text}}</a>
                    </li>
                </ul>
                <div class="customOption" v-show="showType != 'carstyle'">
                    <p>{{customText}}</p>
                    <div class="inputs">
                        <input type="text" id="from" placeholder="最低" ref="from">
                        <span>--</span>
                        <input type="text" id="to" placeholder="最高" ref="to">
                    </div>
                    <div class="m-btn">
                        <button class="weui-btn" @click="sureClick">确定</button>
                    </div>
                </div>
            </div>
        </div>        
        <div class="modal" v-if="isShow"></div>
    </section>
</template>

<script>
export default {
    data(){
        return {
            firstPay:[{text:"不限",value:"null-null"},{text:"1万以内",value:'0-1'},{text:"1-2万",value:'1-2'},{text:"2-3万",value:"2-3"},{text:"3-4万",value:'3-4'},{text:"4-5万",value:"4-5"},{text:"5万以上",value:"5-null"}],
            carPrice:[{text:"不限",value:"null-null"},{text:"10万以内",value:'0-10'},{text:"10-20万",value:'10-20'},{text:"20-50万",value:"20-50"},{text:"50-80万",value:'50-80'},{text:"80-100万",value:"80-100"},{text:"100万以上",value:"100-null"}],
            carStyle:[{text:"",value:""},{text:"小型车",value:"060001"},{text:"中型车",value:"060002"},{text:"SUV",value:"060003"},{text:"MPV",value:"060004"},{text:"跑车",value:"060005"}],
            searchOption:{brandId:null,fromFirstPayment:null,toFirstPayment:null,fromMonthPayment:null,toMonthPayment:null,fromCarCost:null,toCarCost:null,carLevelCd:null},
            showArr:[],
            itemIndex:0,
            firstPaySelcetedIndex:0,
            monthPaySelcetedIndex:0,
            carPriceSelcetedIndex:0,
            carstyleSelcetedIndex:0,
            activeIndex:null,
            customText:'',
            className: "price-item",
            isShow:false,
            showType:""
        }
    },
    methods:{
        optionClick(e,type){
            this.showType = type;
            this.isShow = true;
            let index = e.target.getAttribute('data-index');
            this.activeIndex = index;
            if(this.showType == 'firstpay' && this.firstPaySelcetedIndex != 0){
                this.itemIndex = this.firstPaySelcetedIndex;              
            }else if(this.showType  == 'monthpay' && this.monthPaySelcetedIndex !=0){
                this.itemIndex = this.monthPaySelcetedIndex;
            }else if(this.showType  == 'carprice' && this.carPriceSelcetedIndex !=0){
                this.itemIndex = this.carPriceSelcetedIndex;
            }else if(this.showType  == 'carstyle' && this.carstyleSelcetedIndex != 0){
                this.itemIndex = this.carstyleSelcetedIndex;
            }else{
                this.itemIndex = 0;
            }           
            this.showArr = this.createDropmenu();
            this.$refs.from.value = '';
            this.$refs.to.value = '';
        },
        createDropmenu(){      
            if(this.showType == 'firstpay'){
                this.customText = "自定义首付（万）";
                return this.firstPay;
            }
            if(this.showType  == 'monthpay' ){
                this.customText = "自定义月供（元）";
                return this.firstPay;
            }
            if(this.showType  == 'carprice'){
                this.customText = "自定义车价（万）";
                return this.carPrice;
            }
            if(this.showType  == 'carstyle' ){
                return this.carStyle
            }
           
        },
        setOption(val,index){
            this.itemIndex = index;
            this.isShow = false;
            this.activeIndex = null;
            let value;
            if(val.indexOf('-') <0){
                value = val;
            }else{
                value = val.split('-');
            }
            if(this.showType == 'firstpay'){
                this.firstPaySelcetedIndex = index;
                this.searchOption.fromFirstPayment = value[0];
                this.searchOption.toFirstPayment = value[1];
                this.$refs.from.value = value[0] == 'null' ? '': value[0];
                this.$refs.to.value = value[1] == 'null' ? '': value[1];
            }
            if(this.showType  == 'monthpay' ){
                this.monthPaySelcetedIndex = index;
                this.searchOption.fromMonthPayment = value[0];
                this.searchOption.toMonthPayment = value[1];
                this.$refs.from.value = value[0] == 'null' ? '': value[0];
                this.$refs.to.value = value[1] == 'null' ? '': value[1];
            }
            if(this.showType  == 'carprice'){
                this.carPriceSelcetedIndex = index;
                this.searchOption.fromCarCost = value[0];
                this.searchOption.toCarCost = value[1];
                this.$refs.from.value = value[0] == 'null' ? '': value[0];
                this.$refs.to.value = value[1] == 'null' ? '': value[1];
            }
            if(this.showType  == 'carstyle' ){
                this.carstyleSelcetedIndex = index;
                this.searchOption.carLevelCd = value;
            }

        },
        sureClick(){
             if(this.showType == 'firstpay'){
                this.searchOption.fromFirstPayment = this.$refs.from.value == "" ? null : this.$refs.from.value;
                this.searchOption.toFirstPayment = this.$refs.to.value == "" ? null : this.$refs.to.value ;
            }
            if(this.showType  == 'monthpay' ){
                this.searchOption.fromMonthPayment =  this.$refs.from.value == "" ? null : this.$refs.from.value;
                this.searchOption.toMonthPayment = this.$refs.to.value == "" ? null : this.$refs.to.value ;
            }
            if(this.showType  == 'carprice'){
                this.searchOption.fromCarCost = this.$refs.from.value == "" ? null : this.$refs.from.value;
                this.searchOption.toCarCost = this.$refs.to.value == "" ? null : this.$refs.to.value ;
            }
            this.isShow = false;
            this.activeIndex = null;
        },
        resetOption(){
            this.searchOption = {brandId:null,fromFirstPayment:null,toFirstPayment:null,fromMonthPayment:null,toMonthPayment:null,fromCarCost:null,toCarCost:null,carLevelCd:null},
            this.carstyleSelcetedIndex = 0;
            this.carPriceSelcetedIndex = 0;
            this.monthPaySelcetedIndex = 0;
            this.firstPaySelcetedIndex = 0;
            this.isShow = false;
            this.activeIndex = null;
        },
        getBrand(){
            this.$router.push({
                path:'/mobileSearchCar/brandList'
            })
        },
        watchData(culval,oldval){
            this.$emit('getOptions', culval)
        },
        watchTypeChange(culval,oldval){
            if(culval == 'carstyle'){
                this.className = "style-item";
            }else{
                 this.className = "price-item";
            }
        }
    },
    watch:{
        searchOption:{
            handler:"watchData",
            deep:true
        },
        showType:"watchTypeChange"        
    }


}
</script>



