<template>
    <div class="search-car">
        <v-header>惠搜车</v-header>
        <v-searchbar @getOptions = "getOptions"></v-searchbar>
         <scroll :data="carArr" @scroll="scroll" :listen-scroll="listenScroll" :probe-type="probeType" :pullup="isShow"  :pulldown="isShow" @pulldown="pulldown"  @scrollToEnd="pullup"  class="car-list" ref="carList">
             <div class="list-wraper">
                  <v-carList :carArr="carArr" :car-type ='1'></v-carList>
             </div>           
             <div v-show="!carArr.length" class="loading-container">
                <loading></loading>
            </div>
         </scroll>
        <router-view></router-view>
    </div>
</template>

<script>
import vHeader from '@/components/mobile/m-header'
import vSearchbar from '@/components/mobile/m-search'
import vCarList from '@/components/mobile/m-carList'
import Scroll from '@/components/base/scroll/scroll'
import Loading from '@/components/base/loading/loading'
import {MOBILE_COMMONURL,SUCCESS_CODE} from '@/assets/js/common'
import {mapGetters} from 'vuex'
export default {
    data:function(){
        return {
           carArr:[],
           scrollY: 0,
           isShow: true,  
           page:1,
           selectOptions:{} 
        }
    },
    created() {
      this.probeType = 3
      this.listenScroll = true
      console.log(this.brand);
    },
    components:{
        vHeader,
        vSearchbar,
        vCarList,
        Scroll,
        Loading
    },
    methods:{
        pulldown(){  
            this.carArr = [];
            this.page = 1;
            //下拉刷新重新初始化             
            this.$refs.carList.$emit('pullrefresh.finishLoad');  
            this.createJson();
        },  
        pullup(){  
            if(!this.page) return  
            this.page ++   
            //上拉加载重新初始化  
            this.$refs.carList.$emit('infinitescroll.reInit');
            this.createJson(this.page);  
        },  
        getOptions(data){
            this.selectOptions = data;
            this.carArr = [];
            this.createJson();
        },
        createJson(page){
            let json = {};
            json.pageSize = 10;
            if(page){
                json.pageNum = page;
            }else{
                json.pageNum = 1
            }
            if(Object.keys(this.selectOptions).length != 0){                    
                json.brandId = this.selectOptions.brandId;
                json.fromFirstPayment = this.selectOptions.fromFirstPayment;
                json.toFirstPayment = this.selectOptions.toFirstPayment;
                json.fromMonthPayment = this.selectOptions.fromMonthPayment;
                json.toMonthPayment = this.selectOptions.toMonthPayment;
                json.fromCarCost = this.selectOptions.fromCarCost;
                json.toCarCost = this.selectOptions.toCarCost;
                json.carLevelCd = this.selectOptions.carLevelCd;               
            }else{
                json.brandId=null;
                json.fromFirstPayment=null;
                json.toFirstPayment=null;
                json.fromMonthPayment=null;
                json.toMonthPayment=null;
                json.fromCarCost=null;
                json.toCarCost=null;
                json.carLevelCd=null;
            }
            this.getcarData(json);
        },
        getcarData:function(data){
            let _this = this;
            $.ajax({
                url:MOBILE_COMMONURL+"/queryLeaseCarList",
                type:"get",
                data:data,
                dataType:'json',
                asnyc:true,
                success:function(data){
                    console.log(data);
                    if(data.code == SUCCESS_CODE){
                        if(data.data.leaseCarList.length > 0){  
                            _this.carArr = [..._this.carArr, ...data.data.leaseCarList]  
                            //单次请求数据加载完毕后  
                            _this.$refs.carList.$emit('infinitescroll.finishLoad');  
                        }else{  
                            //把page设置成false 数据已经加载完毕了 不用在加载了  
                            _this.page = false  
                            //所有数据加载完毕后  
                            _this.$refs.carList.$emit('infinitescroll.loadedDone');  
                        }
                    }
                }
            })
        },
        scroll(pos) {
            this.scrollY = pos.y;
            console.log(pos.y);
        },
    },
    computed:{
        ...mapGetters([
            'brand'
        ])
    },
    mounted(){
        this.createJson();
        this.$refs.carList.$el.style.top = `80px`;       
    }
}
</script>



